<template>
    <div class="sidebar" :style="sidebarStyle">
        <!--   -->
        <div class="profile">
            <!--   -->
            <div class="profileImg">
                <img src="@/assets/images/dog2.jpg">
            </div>
            <!--   -->
            <div class="profileId">
                <label>dddddd</label>  <br>
                <label> : 1500</label> P
            </div>
        </div>

        <!--  -->
        <div class="profilePetBox">

            <!--    -->
            <div class="profileTitle">
                <p></p>
                <router-link to="/petaddform"></router-link>
            </div>

            <!--    -->
            <div class="petProfileBox">
                <!--  1 -->
                <div class="profilePet">
                    <img src="@/assets/images/spy.jpg">
                    <p>(4)</p>
                </div>
                <!--  2 -->
                <div class="profilePet">
                    <img src="@/assets/images/dog.jpg">
                    <p>(3)</p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import '@/assets/css/mypage/sidebar.css'
export default {
    name: "SideBar",
    components: {
    },

    data() {
        return {
            isFixed: false,
        };
    },
    computed:{
        //   
        sidebarStyle(){
            return{
                position: this.isFixed ? 'fixed' : 'fixed',
                top: this.isFixed ? '0': '138px',
                transition: 'top 0.3s ease',
            };
        },
    },
    mounted(){
        window.addEventListener('scroll', this.handleScroll);
    },
    beforeUnmount() {
        window.removeEventListener('scroll', this.handleScroll); //     
    },
    methods: {
        //  
        handleScroll(){
            const scrollTop = window.scrollY || window.pageYOffset; //   
            if (scrollTop > 138) {
                //   138px 
                this.isFixed = true; //    
            } else {
                this.isFixed = false; //     
            }
        },
    },
    created(){
        
    }
};
</script>

