<template>
    <div id="wrap">
        <AppHeader />
        <div id="petAddForm">
            <SideBar />
            <div class="container">
                <h1></h1>
                <form>
                    <div class="filebox preview-image">
                        <img id="preview-image">
                        <label for="input-file"></label>
                        <input type="file" id="input-file" class="upload-hidden" @change="handleFileUpload">
                    </div>

                    <div class="detailBox">
                        <div class="detail">
                            <label> </label>
                            <input type="text" placeholder="  .">
                        </div>
                        <div class="detail">
                            <label></label>
                            <input type="text" placeholder="  .">
                        </div>
                        <div class="detail">
                            <label class="title"></label>
                            <input type="text"> kg
                        </div>
                        <div class="detail" id="petAge">
                            <label></label>
                            <input type="text"> 

                        </div>
                        <div class="detail">
                            <label for=""></label>
                            <button type="button"></button>
                            <button type="button"></button>
                        </div>
                        <div class="detail">
                            <label></label>
                            <button type="button">O</button>
                            <button type="button">X</button>
                        </div>
                        <div class="detail">
                            <label></label>
                            <button type="button">O</button>
                            <button type="button">X</button>
                        </div>
                        <div class="detail" id="feature">
                            <label></label>
                            <button type="button"></button>
                            <button type="button"></button>
                            <button type="button"></button>
                            <button type="button"></button>
                            <div class="bite">
                                <div class="biteRange">
                                    <label></label>
                                    <div class="biteRange2">
                                        <input type="range" min="1" max="3" step="1" list="tickmarks">
                                        <datalist id="tickmarks">
                                            <option value="1"></option>
                                            <option value="2"></option>
                                            <option value="3"></option>
                                        </datalist>
                                    </div>
                                </div>
                                <div class="pbox">
                                    <p></p>
                                    <p></p>
                                    <p></p>
                                </div>



                            </div>
                            <textarea placeholder="  ."></textarea>
                        </div>
                    </div>
                </form>
                <div class="insertBtnBox">
                    <button class="insertBtn" type=""></button>
                </div>
            </div>
        </div>
        <TopButton />
        <AppFooter />
    </div><!-- wrap -->
</template>

<script>
import AppFooter from "@/components/AppFooter.vue"
import AppHeader from "@/components/AppHeader.vue"
import TopButton from "@/components/TopButton.vue";
import SideBar from "@/components/SideBar.vue";

import "@/assets/css/mypage/mypage.css"

export default {
    name: "PetAddFormView",
    components: {
        TopButton,
        AppHeader,
        AppFooter,
        SideBar,
    },

    data() {
        return {
        };
    },
    methods: {
        handleFileUpload(event) {
            const file = event.target.files[0];
            const reader = new FileReader();

            if (!file.type.match(/image\//)) {
                alert("    .");
                return;
            }

            reader.onload = () => {
                const imgSrc = reader.result;
                //      
                //    
                const imgElement = document.getElementById('preview-image'); //   <img>  
                imgElement.src = imgSrc; //     
            };

            reader.readAsDataURL(file);
        }


    },
};
</script>

