<template>
  <ManagerHeader /> <!--    -->
  <div class="total-container">

    <!--   -->
    <div class="chart-container">
      <div style="display: flex;">
        <div>
          <!--    -->
          <canvas width="418" height="410" ref="dailyChartCanvas"></canvas>
          <div class="total-sales">  : {{ dailyTotal }}</div>
        </div>
        <!--   -->
        <div class="sales-table">
          <table>
            <thead>
              <tr>
                <th></th>
                <th> </th>
                <th> </th>
                <th> </th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(sales, index) in dailySales" :key="index">
                <td>{{ index + 1 }}</td>
                <td>{{ sales.scissor }}</td>
                <td>{{ sales.partial }}</td>
                <td>{{ sales.full }}</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>

    <!--   -->
    <div class="chart-container">
      <!--    -->
      <canvas width="300" height="200" ref="weeklyChartCanvas"></canvas>
      <div class="total-sales">  : {{ weeklyTotal }}</div>
    </div>

    <!--   -->
    <div class="chart-container">
      <!--    -->
      <canvas width="300" height="200" ref="monthlyChartCanvas"></canvas>
      <div class="total-sales">  : {{ monthlyTotal }}</div>
    </div>

    <!--   -->
    <div class="chart-container">
      <!--    -->
      <canvas width="300" height="200" ref="yearlyChartCanvas"></canvas>
      <div class="total-sales">  : {{ yearlyTotal }}</div>
    </div>

  </div>
  <ManagerFooter /> <!--    -->
</template>

<script>
import ManagerFooter from "@/components/ManagerFooter.vue";
import ManagerHeader from "@/components/ManagerHeader.vue";
import Chart from "chart.js/auto";
import "@/assets/css/manager/totalsales.css";

export default {
  name: "TotalSalesView", //  
  components: {
    ManagerFooter,
    ManagerHeader,
  },
  data() {
    return {
      dailySales: [ //    
        { scissor: 50, partial: 30, full: 20 },
        { scissor: 60, partial: 40, full: 25 },
        { scissor: 70, partial: 35, full: 30 },
        { scissor: 80, partial: 50, full: 35 },
        { scissor: 90, partial: 45, full: 40 },
        { scissor: 100, partial: 55, full: 50 },
        { scissor: 110, partial: 60, full: 55 }
      ],
      weeklySales: [], //    
      monthlySales: [], //    
      yearlySales: [], //    
    };
  },
  computed: {
    //   
    dailyTotal() {
      return this.dailySales.reduce((total, sales) => total + sales.scissor + sales.partial + sales.full, 0);
    },
    weeklyTotal() {
      return this.weeklySales.reduce((total, sale) => total + sale, 0);
    },
    monthlyTotal() {
      return this.monthlySales.reduce((total, sale) => total + sale, 0);
    },
    yearlyTotal() {
      return this.yearlySales.reduce((total, sale) => total + sale, 0);
    },
  },
  methods: {
    //      
    fetchDailySales() {
      this.dailySales = [100, 200, 300, 400, 500, 600, 700]; // 
    },
    fetchWeeklySales() {
      this.weeklySales = [500, 600, 700, 800]; // 
    },
    fetchMonthlySales() {
      this.monthlySales = [1500, 1600, 1700, 1800, 1900, 2000]; // 
    },
    fetchYearlySales() {
      this.yearlySales = [18000, 19000, 20000, 21000, 22000]; // 
    },
    //   
    drawChart(canvasRef, labels, data, backgroundColor, borderColor, label, type) {
      new Chart(canvasRef, {
        type: type,
        data: {
          labels: labels,
          datasets: [{
            label: label,
            data: data,
            backgroundColor: backgroundColor,
            borderColor: borderColor,
            borderWidth: 1
          }]
        },
        options: {
          scales: {
            y: {
              beginAtZero: true
            }
          },
          plugins: {
            legend: {
              position: 'right', //    
            },
          },
        }
      });
    },
  },
  mounted() {
    //   .
    this.drawChart(this.$refs.dailyChartCanvas, ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'], this.dailySales, 'rgba(255, 99, 132, 0.2)', 'rgba(255, 99, 132, 1)', ' ', 'doughnut');
    this.drawChart(this.$refs.weeklyChartCanvas, ['1', '2', '3', '4'], this.weeklySales, 'rgba(54, 162, 235, 0.2)', 'rgba(54, 162, 235, 1)', ' ', 'line');
    this.drawChart(this.$refs.monthlyChartCanvas, ['1', '2', '3', '4', '5', '6'], this.monthlySales, 'rgba(255, 206, 86, 0.2)', 'rgba(255, 206, 86, 1)', ' ', 'bar');
    this.drawChart(this.$refs.yearlyChartCanvas, ['2021', '2022', '2023', '2024'], this.yearlySales, 'rgba(75, 192, 192, 0.2)', 'rgba(75, 192, 192, 1)', ' ', 'bar');
  },
  created() {
    //          
    this.fetchDailySales();
    this.fetchWeeklySales();
    this.fetchMonthlySales();
    this.fetchYearlySales();
  }
};
</script>

<style scoped>
