<template>
    <div>
        <ManagerHeader /> <!--    -->
  
        <div class="calendar-container">
            <FullCalendar :options="calendarOptions" id="calendar" />
        </div>
  
        <ManagerFooter /> <!--    -->
    </div>
</template>

<script>
import ManagerFooter from "@/components/ManagerFooter.vue";
import ManagerHeader from "@/components/ManagerHeader.vue";
import FullCalendar from "@fullcalendar/vue3";
import dayGridPlugin from "@fullcalendar/daygrid";
import Swal from "sweetalert2";
import "@/assets/css/manager/schedule.css"; //    CSS 

export default {
    name: "ScheduleView", //  
  
    components: {
        ManagerFooter,
        ManagerHeader,
        FullCalendar
    },
    data() {
        return {
            calendarOptions: {
                plugins: [dayGridPlugin],
                initialView: "dayGridMonth",
                headerToolbar: {
                    start: "prev,next today",
                    center: "title",
                    end: "dayGridMonth,dayGridWeek,dayGridDay"
                },
                contentHeight: 800,
                locale: "ko",
                events: [
                    { title: ",  , 50,000", start: "2024-05-10T09:00:00", end: "2024-05-10T12:00:00" },
                    { title: ", ,  , 50,000", start: "2024-05-14T14:00:00", end: "2024-05-13T11:00:00" },
                    { title: ",  , 50,000", start: "2024-05-15T15:00:00", end: "2024-05-15T17:00:00" }
                    //    
                ],
                eventClick: this.handleEventClick
            }
        };
    },
    methods: {
        handleEventClick(info) {
            Swal.fire({
                title: "",
                html: ": " + info.event.title + "<br/>: " + new Date(info.event.start).toLocaleString().substring(0, 20).replace("/g", ""),
            });

            //    
            this.$store.commit("setSelectedSchedule", info.event.title);
            //   
            this.$router.push("/diary");
        }
    }
};
</script>