<template>
    <div id="wrap">
        <div id="mlogin-container">
            <div class="mlogin-box">
                <router-link to="/">
                    <img class="con-logo" src="../../assets/images/logo.png" alt="Logo">
                </router-link>
                <div class="login-form">
                    <form>
                        <div class="form-group-login">
                            <input type="text" id="id" name="id" placeholder="">
                        </div>
                        <div class="form-group-login">
                            <input type="password" id="password" name="password" placeholder="">
                            <span class="toggle-password" @click="togglePasswordVisibility">
                                <i :class="showPassword ? 'fas fa-eye' : 'fas fa-eye-slash'"></i>
                            </span>
                        </div>
                        <button type="submit" class="loginBtn"></button>
                        <router-link to="/msignup">
                            <button class="signinBtn"></button>
                        </router-link>
                    </form>
                </div>
            </div><!-- login-box -->
            <div class="login-image-container">
                <img src="../../assets/images/banner02.png" alt="Image">
            </div>
            <div class="login-footer">
                <ul class="login-footer-ul">
                    <li></li>
                    <li></li>
                    <li>  </li>
                    <li> </li>
                </ul>
                <p> 2024  . All Rights Reserved.</p>
            </div>
        </div><!-- login-container -->
    </div><!-- wrap -->
</template>

<script>
import "@/assets/css/potal/mlogin.css"
export default {
    name: "ManagerLoginView",
    components: {
    },
    data() {
        return {
            showPassword: false //   
        };
    },
    methods: {
        togglePasswordVisibility() {
            this.showPassword = !this.showPassword; //  / 
            const passwordInput = document.getElementById('password');
            if (this.showPassword) {
                passwordInput.type = 'text'; //  
            } else {
                passwordInput.type = 'password'; //  
            }
        }
    },
};
</script>